<template>
  <div>
    <!-- contoh <JsonToExcel :data='data.json' /> -->
    <span @click="jsonToExcel" class="btn btn-sm btn-outline-success">
      <i class="far fa-file-excel"></i>
      <span style="font-size:10px;">
        <img src="@/static/icon/excell.png" style="height:30px;" alt />
      </span>
    </span>
  </div>
</template>

<script>
import { json2excel, excel2json } from "js2excel";
export default {
  props: {
    data: {
      type: Array,
      required: true
    }
  },
  methods: {
    jsonToExcel() {
      let that = this;
      try {
        document.addEventListener("deviceready", function(){
          json2excel({
            data: that.data,
            name: "filebaru",
            formateDate: "yyyy/mm/dd"
          });
        }, false);
      } catch (e) {
        alert('error')
        console.error("export error");
      }
    }
  },
  mounted() {
    let that = this;
    this.$nuxt.$on("backExcel", data => {
      that.jsonToExcel();
    });
  }
};
</script>
